import { Component, OnInit, Input, EventEmitter, Output, ViewChild, ElementRef } from '@angular/core';



export class SelectInputComponent implements OnInit {
  
  inputElement: ElementRef;
  
  inFormGroup: boolean = true;
  
  focused: boolean = false;
  
  items: any[] = [];
  
  readonly: boolean = false;
  
  name: string = 'select';
  
  placeholder: string = '';
  
  valueField: string = 'id';
  
  title: string;
  
  model: any;
  
  titleField: string = 'title';
  
  modelChange: EventEmitter<any> = new EventEmitter<any>();

  public value: any;
  
  errors: EventEmitter<any> = new EventEmitter<any>();
  
  info: EventEmitter<any> = new EventEmitter<any>();
  private errorsValue: any;
  private infoValue: any;
  ngOnInit() {
    this.errors.subscribe((data: any) => {
      this.errorsValue = data;
      let keys = Object.keys(data);
      if (keys[0] === this.name) {
        this.focus();
      }
    });
    this.info.subscribe((data: any) => {
      this.infoValue = data;
      let keys = Object.keys(data);
      if (keys[0] === this.name) {
        this.focus();
      }
    });
    this.init();
  }
  init() {
    if (this.model) {
      this.value = this.getValue(this.model);
    } else {
      if (this.items && this.items.length > 0) {
        this.value = this.getValue(this.items[0]);
        this.updateModel();
      } else {
        this.value = this.getValue(null);
      }
    }
    setTimeout((out: any) => {
      if (this.focused === true) {
        this.focus();
      }
    }, 700);
  }
  focus() {
    this.inputElement.nativeElement.focus();
  }
  getValue(item: any) {
    if (item !== null && item[this.valueField]) {
      return item[this.valueField];
    } else {
      return null;
    }
  }
  getTitle(item: any) {
    if (item !== null && item[this.titleField]) {
      return item[this.titleField];
    } else {
      return null;
    }
  }
  updateModel() {
    setTimeout((out: any) => {
      let items = this.items.filter(item => this.getValue(item) === this.value);
      if (this.items && items.length > 0) {
        this.model = items[0];
      } else {
        this.model = null;
      }
      this.modelChange.emit(this.model);
    }, 700);
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'select-input',
  template: `
    <div class="form-group" [ngClass]="{'has-error':errorsValue && errorsValue[name]}" *ngIf="inFormGroup">
      <label class="control-label" [attr.for]="'input'+name">{{title | translate}}</label>
      <select class="form-control" [attr.id]="'input'+name" [attr.name]="'input'+name" [attr.placeholder]="placeholder" [(ngModel)]="value"
        [disabled]="readonly" (change)="updateModel()" #inputElement>
        <option *ngFor="let item of items" [value]="getValue(item)">{{getTitle(item) | translate}}</option>
      </select>
      <span class="help-block" *ngIf="errorsValue && errorsValue[name]">{{errorsValue[name].join(', ')}}</span>
    </div>
    <select class="form-control" [attr.id]="'input'+name" [attr.name]="'input'+name" [attr.placeholder]="placeholder" [(ngModel)]="value"
      [disabled]="readonly" (change)="updateModel()" *ngIf="!inFormGroup" #inputElement>
        <option *ngFor="let item of items" [value]="getValue(item)">{{getTitle(item)}}</option>
    </select>
  `,
  styles: [`
    .select-input {

    }
  `]
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'inputElement': [{ type: ViewChild, args: ['inputElement', ] },],
'inFormGroup': [{ type: Input },],
'focused': [{ type: Input },],
'items': [{ type: Input },],
'readonly': [{ type: Input },],
'name': [{ type: Input },],
'placeholder': [{ type: Input },],
'valueField': [{ type: Input },],
'title': [{ type: Input },],
'model': [{ type: Input },],
'titleField': [{ type: Input },],
'modelChange': [{ type: Output },],
'errors': [{ type: Input },],
'info': [{ type: Input },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}



