import {
  Component,
  EventEmitter,
  Input,
  Output,
  ViewChild,
  OnInit
} from '@angular/core';
import { User } from './../../../shared/models/user.model';
import { ModalDirective } from 'ng2-bootstrap';
import { AccountService } from '../../../shared/account.service';
import { TextInputComponent } from '../../../controls/text-input/text-input.component';
import { UserGroupsGridComponent } from '../../user-groups-grid/user-groups-grid.component';
import { Group } from '../../../shared/models/group.model';
import { UserGroup } from '../../../shared/models/user-group.model';



export class UserModalComponent implements OnInit {

  
  modal: ModalDirective;
  
  focusElement: TextInputComponent;
  
  userGroups: UserGroupsGridComponent;
  
  text: string = '';
  
  class: string = '';
  
  readonly: boolean = false;
  
  hideOnClose?: boolean = true;
  
  account: any | User = null;
  
  title: string = '';
  
  item: any | User = new User();
  
  public modelMeta: any = User.meta;
  
  onClose: EventEmitter<UserModalComponent | any> = new EventEmitter();
  
  onSave: EventEmitter<UserModalComponent | any> = new EventEmitter();

  public errors: EventEmitter<any> = new EventEmitter();
  public info: EventEmitter<any> = new EventEmitter();

  ngOnInit() {
    this.modal.onHidden.subscribe(() => this.close());
    this.modal.onShown.subscribe(() => {
      this.focus();
      this.userGroups.user = this.item;
      this.userGroups.mockedItems =
        this.item.groups.map((group: Group) => {
          return new UserGroup({
            id: group.pk,
            group: group
          });
        });
      this.userGroups.search();
    });
  }
  focus() {
    this.focusElement.focus();
  }

  close() {
    if (this.hideOnClose && this.modal.isShown) {
      this.modal.hide();
    }
    this.onClose.emit(this);
    return false;
  }
  save() {
    this.item.groups =
      this.userGroups.mockedItems.map((userGroup: UserGroup) => userGroup.group);
    this.onSave.emit(this);
    return false;
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'user-modal',
  template: `
    <div bsModal #modal="bs-modal" class="modal fade user-modal" [ngClass]="[class]" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel"
      aria-hidden="true">
      <div class="modal-dialog modal-lg">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" aria-label="Close" (click)="close()">
                        <span aria-hidden="true">&times;</span>
                    </button>
            <h4 class="modal-title">{{title | translate}}</h4>
          </div>
          <form (ngSubmit)="save()">
            <div class="modal-body">
              <div class="row">
                <div class="col-md-6">
                  <div class="row">
                    <div class="col-md-6">
                      <text-input [errors]="errors" [info]="info" name="username" [(model)]="item.username" [title]="modelMeta.titles.username" [focused]="true"
                        [readonly]="readonly" #focusElement></text-input>
                    </div>
                    <div class="col-md-6">
                      <text-input [errors]="errors" [info]="info" type="email" name="email" [(model)]="item.email" [title]="modelMeta.titles.email" [readonly]="readonly"></text-input>
                    </div>
                    <div class="col-md-6">
                      <text-input [errors]="errors" [info]="info" type="password" name="password" [(model)]="item.password" [title]="modelMeta.titles.password"
                        [readonly]="readonly"></text-input>
                    </div>
                    <div class="col-md-6">
                      <text-input [errors]="errors" [info]="info" type="password" name="rePassword" [(model)]="item.rePassword" [title]="modelMeta.titles.rePassword"
                        [readonly]="readonly"></text-input>
                    </div>
                    <div class="col-md-6">
                      <text-input [errors]="errors" [info]="info" name="lastName" [(model)]="item.lastName" [title]="modelMeta.titles.lastName" [readonly]="readonly"></text-input>
                    </div>
                    <div class="col-md-6">
                      <text-input [errors]="errors" [info]="info" name="firstName" [(model)]="item.firstName" [title]="modelMeta.titles.firstName" [readonly]="readonly"></text-input>
                    </div>
                  </div>
                </div>
                <div class="col-md-6">
                  <checkboxes-input [errors]="errors" name="roles" [(models)]="item.roles" [checkboxesTitles]="modelMeta.titles.rolesTitles" [title]="modelMeta.titles.roles"
                    [readonly]="readonly"></checkboxes-input>
                </div>
                <div class="col-md-6">
                  <user-groups-grid [loadAll]="false" #userGroups [readonly]="readonly"></user-groups-grid>
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <modal-footer-buttons (onClose)="close()" [readonly]="readonly" [okTitle]="text"></modal-footer-buttons>
            </div>
          </form>
        </div>
      </div>
      </div>
  `,
  styles: [`
    .user-modal {
        .field-role{
            display: block;
        }
    }
  `]
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'modal': [{ type: ViewChild, args: ['modal', ] },],
'focusElement': [{ type: ViewChild, args: ['focusElement', ] },],
'userGroups': [{ type: ViewChild, args: ['userGroups', ] },],
'text': [{ type: Input },],
'class': [{ type: Input },],
'readonly': [{ type: Input },],
'hideOnClose': [{ type: Input },],
'account': [{ type: Input },],
'title': [{ type: Input },],
'item': [{ type: Input },],
'modelMeta': [{ type: Input },],
'onClose': [{ type: Output },],
'onSave': [{ type: Output },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}



