import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AccountService } from './../../shared/account.service';
import { AppService } from '../../shared/app.service';
import { User } from '../../shared/models/user.model';
import { TranslateService } from '@ngx-translate/core';



export class AdminPageComponent implements OnInit {

  public title: string;

  get account(): User {
    return this.accountService.account;
  }
  constructor(public router: Router, public accountService: AccountService, public app: AppService,
    public translateService: TranslateService) {
    this.title = this.translateService.instant('Admin');
  }
  ngOnInit() {
    this.init();
  }
  init() {
    this.app.currentPageName = 'admin';
    this.app.currentPageTitle = this.title;
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'admin-page',
  template: `
    <div class="container-fluid">
    	<div class="row">
    		<div class="col-sm-3 col-md-2">
    			<div class="app-sidebar" *ngIf="account">
    				<ul class="nav nav-sidebar">
    					<li [class.active]="router.isActive('/admin/users')" *ngIf="account.checkPermissions(['read_user'])" class="app-sidebar__item">
    						<a routerLink="/admin/users" translate>Users</a>
    					</li>
    					<li [class.active]="router.isActive('/admin/groups')" *ngIf="account.checkPermissions(['read_group'])" class="app-sidebar__item">
    						<a routerLink="/admin/groups" translate>Groups</a>
    					</li>
    				</ul>
    			</div>
    		</div>
    		<div class="col-sm-9 col-md-10">
    			<router-outlet></router-outlet>
    		</div>
    	</div>
    </div>
  `,
  styles: [`
    .admin-page {

    }
  `]
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: Router, },
{type: AccountService, },
{type: AppService, },
{type: TranslateService, },
];
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}



