import { Component, OnInit, Input, Output, ViewChild, EventEmitter, ElementRef, ViewContainerRef } from '@angular/core';
import { ModalDirective } from 'ng2-bootstrap';



export class AlertModalComponent implements OnInit {

  
  focusElement: ElementRef;
  
  modal: ModalDirective;
  
  text: string = '';
  
  messageClass: string = 'text-danger';
  
  buttonClass: string = 'btn-default';
  
  buttonText: string;
  
  hideOnClose?: boolean = true;
  
  size?: string = 'sm';
  
  title?: string = '';
  
  hideButton?: boolean = false;
  
  message: string = '';
  
  onClose: EventEmitter<AlertModalComponent | any> = new EventEmitter();

  ngOnInit() {
    this.modal.onHidden.subscribe(() => this.close());
    this.modal.onShown.subscribe(() => this.focus());
    this.init();
  }
  init() {
    if (this.message.length > 100) {
      this.size = 'md';
    }
    if (this.message.length > 1000) {
      this.size = 'lg';
    }
    if (!this.title) {
      this.title = this.text;
    }
  }
  focus() {
    this.focusElement.nativeElement.focus();
  }

  close() {
    if (this.hideOnClose && this.modal.isShown) {
      this.modal.hide();
    }
    this.onClose.emit(this);
    return false;
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'alert-modal',
  template: `
    <div bsModal #modal="bs-modal" class="modal fade alert-modal" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel"
      aria-hidden="true">
      <div class="modal-dialog" [ngClass]="['modal-'+size]">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" aria-label="Close" (click)="close()">
                        <span aria-hidden="true">&times;</span>
                    </button>
            <h4 class="modal-title">{{title | translate}}</h4>
          </div>
          <form (ngSubmit)="close()">
            <div class="modal-body" [ngClass]="[messageClass]">
              {{message | translate}}
            </div>
            <div class="modal-footer">
              <button type="submit" #focusElement class="btn" [ngClass]="[buttonClass]">{{buttonText}}</button>
            </div>
          </form>
        </div>
      </div>
    </div>
  `,
  styles: [`
    .alert-modal {
    }
  `]
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'focusElement': [{ type: ViewChild, args: ['focusElement', ] },],
'modal': [{ type: ViewChild, args: ['modal', ] },],
'text': [{ type: Input },],
'messageClass': [{ type: Input },],
'buttonClass': [{ type: Input },],
'buttonText': [{ type: Input },],
'hideOnClose': [{ type: Input },],
'size': [{ type: Input },],
'title': [{ type: Input },],
'hideButton': [{ type: Input },],
'message': [{ type: Input },],
'onClose': [{ type: Output },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}



