import { Component, OnInit, EventEmitter, Input, Output, ViewChild, ElementRef } from '@angular/core';
import { User } from './../../../shared/models/user.model';
import { ModalDirective } from 'ng2-bootstrap';
import { UsersGridComponent } from '../users-grid.component';



export class UsersListModalComponent implements OnInit {

  
  text: string;
  
  class: string;
  
  readonly: boolean;
  
  modal: ModalDirective;
  
  focusElement: ElementRef;
  
  onClose: EventEmitter<UsersListModalComponent>;
  
  hideOnClose?: boolean;

  
  users: UsersGridComponent;

  
  account: User;
  
  title: string;
  
  onSave: EventEmitter<UsersListModalComponent>;

  item: User;
  items: User[];
  public modelMeta: any = User.meta;

  public errors: EventEmitter<any> = new EventEmitter();
  public info: EventEmitter<any> = new EventEmitter();

  constructor() {
    if (this.hideOnClose === undefined) {
      this.hideOnClose = true;
    }
    this.onClose = new EventEmitter<UsersListModalComponent>();
    this.item = new User();
    this.onSave = new EventEmitter<UsersListModalComponent>();
  }

  ngOnInit() {
    this.modal.onHidden.subscribe(() => this.close());
    this.modal.onShown.subscribe(() => this.focus());
  }

  focus() {
    this.users.focus();
  }

  selectUser(items: User[]) {
    this.item = items[0];
    this.items = items;
  }

  close() {
    if (this.hideOnClose && this.modal.isShown) {
      this.modal.hide();
    }
    this.onClose.emit(this);
    return false;
  }

  select() {
    this.onSave.emit(this);
    return false;
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'users-list-modal',
  template: `
    <div bsModal #modal="bs-modal" class="modal fade user-modal" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel"
      aria-hidden="true">
      <div class="modal-dialog modal-lg">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" aria-label="Close" (click)="close()">
                        <span aria-hidden="true">&times;</span>
                    </button>
            <h4 class="modal-title">{{title | translate}}</h4>
          </div>
          <div class="modal-body">
            <users-grid #users (onSelectItems)="selectUser($event)"></users-grid>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-default" (click)="close()" translate>Close</button>
            <button type="button" class="btn btn-success" (click)="select()">{{text | translate}}</button>
          </div>
        </div>
      </div>
    </div>
  `,
  styles: [`
    .users-list-modal {
    }
  `]
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'text': [{ type: Input },],
'class': [{ type: Input },],
'readonly': [{ type: Input },],
'modal': [{ type: ViewChild, args: ['modal', ] },],
'focusElement': [{ type: ViewChild, args: ['focusElement', ] },],
'onClose': [{ type: Output },],
'hideOnClose': [{ type: Input },],
'users': [{ type: ViewChild, args: ['users', ] },],
'account': [{ type: Input },],
'title': [{ type: Input },],
'onSave': [{ type: Output },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}



