import { Component, Output, Input, ViewChild, EventEmitter, ElementRef, OnInit } from '@angular/core';
import { ModalDirective } from 'ng2-bootstrap';
import { User } from './../../shared/models/user.model';
import { AppService } from '../../shared/app.service';
import { TextInputComponent } from '../../controls/text-input/text-input.component';



export class AuthModalComponent implements OnInit {

  
  modal: ModalDirective;
  
  focusElement: TextInputComponent;
  
  title: string = '';
  
  class: string = '';
  
  hideOnClose?: boolean = true;
  
  hideButton?: boolean = false;
  
  account: any | User = null;
  
  onClose: EventEmitter<AuthModalComponent | any> = new EventEmitter();
  
  onLogin: EventEmitter<AuthModalComponent | any> = new EventEmitter();

  public errors: EventEmitter<any> = new EventEmitter();
  public info: EventEmitter<any> = new EventEmitter();

  public modelMeta: any = User.meta;

  ngOnInit() {
    this.modal.onHidden.subscribe(() => this.close());
    this.modal.onShown.subscribe(() => this.focus());
    this.init();
  }
  init() {
    this.account = new User();
  }
  focus() {
    this.focusElement.focus();
  }
  close() {
    if (this.hideOnClose && this.modal.isShown) {
      this.modal.hide();
    }
    this.onClose.emit(this);
    return false;
  }
  login() {
    this.onLogin.emit(this);
    return false;
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'auth-modal',
  template: `
    <div bsModal #modal="bs-modal" class="modal fade auth-modal" [ngClass]="[class]" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel"
      aria-hidden="true">
      <div class="modal-dialog modal-sm">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" aria-label="Close" (click)="close()">
                        <span aria-hidden="true">&times;</span>
                    </button>
            <h4 class="modal-title">{{title | translate}}</h4>
          </div>
          <form (ngSubmit)="login()">
            <div class="modal-body">
              <text-input [errors]="errors" [info]="info" name="username" [(model)]="account.username" [title]="modelMeta.titles.usernameOrEmail" [focused]="true"
                [readonly]="readonly" #focusElement></text-input>
              <text-input [errors]="errors" [info]="info" type="password" name="password" [(model)]="account.password" [title]="modelMeta.titles.password"
                [readonly]="readonly"></text-input>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-default" (click)="close()" translate>Cancel</button>
              <button type="submit" class="btn btn-primary" translate>Login</button>
            </div>
          </form>
        </div>
      </div>
    </div>
  `,
  styles: [`
    .auth-modal {
    }
  `]
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'modal': [{ type: ViewChild, args: ['modal', ] },],
'focusElement': [{ type: ViewChild, args: ['focusElement', ] },],
'title': [{ type: Input },],
'class': [{ type: Input },],
'hideOnClose': [{ type: Input },],
'hideButton': [{ type: Input },],
'account': [{ type: Input },],
'onClose': [{ type: Output },],
'onLogin': [{ type: Output },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}



