import { Component, OnInit, Input, EventEmitter, Output, ViewChild, ElementRef } from '@angular/core';



export class CheckboxesInputComponent implements OnInit {
  
  inputElement: ElementRef;
  
  focused: boolean = false;
  
  readonly: boolean = false;
  
  name: string = 'checkboxes';
  
  title: string = '';
  
  models: { [key: string]: boolean; } = {};
  
  checkboxesTitles: { [key: string]: string; } = {};
  
  modelsChange: EventEmitter<{ [key: string]: boolean; }> = new EventEmitter<{ [key: string]: boolean; }>();

  public values: { [key: string]: boolean; } = {};
  
  errors: EventEmitter<any> = new EventEmitter<any>();
  
  info: EventEmitter<any> = new EventEmitter<any>();
  private errorsValue: any;
  private infoValue: any;
  ngOnInit() {
    this.errors.subscribe((data: any) => {
      this.errorsValue = data;
      let keys = Object.keys(data);
      if (keys[0] === this.name) {
        this.focus();
      }
    });
    this.info.subscribe((data: any) => {
      this.infoValue = data;
      let keys = Object.keys(data);
      if (keys[0] === this.name) {
        this.focus();
      }
    });
    this.init();
  }
  init() {
    this.values = this.models;
    setTimeout((out: any) => {
      if (this.focused === true) {
        this.focus();
      }
    }, 700);
  }
  focus() {
    this.inputElement.nativeElement.focus();
  }
  updateModels() {
    setTimeout((out: any) => {
      this.models = this.values;
      this.modelsChange.emit(this.models);
    }, 700);
  }
  keys(object: {}) {
    return Object.keys(object);
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'checkboxes-input',
  template: `
    <div class="form-group checkboxes-input" [ngClass]="{'has-error':errorsValue && errorsValue[name]}">
      <label class="control-label">{{title | translate}}</label>
      <div class="form-control form-checkbox-controls">
        <label class="control-label checkbox-inline" *ngFor="let key of keys(values)" [hidden]="!checkboxesTitles[key]">
            <input type="checkbox" [attr.id]="'input'+key" [(ngModel)]="values[key]" (change)="updateModels()" [disabled]="readonly" [ngModelOptions]="{standalone: true}"> {{ checkboxesTitles[key] | translate }}
        </label>
      </div>
      <span class="help-block" *ngIf="errorsValue && errorsValue[name]">{{errorsValue[name].join(', ')}}</span>
    </div>
  `,
  styles: [`
    .checkboxes-input {
        .form-checkbox-controls{
            border: 0px;
            padding-right: 0px;
            padding-left: 0px;
            box-shadow: none;
            background-color: transparent;
            .checkbox-inline:first-child{
            //    padding-left:0px;
            }
        }
    }
  `]
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'inputElement': [{ type: ViewChild, args: ['inputElement', ] },],
'focused': [{ type: Input },],
'readonly': [{ type: Input },],
'name': [{ type: Input },],
'title': [{ type: Input },],
'models': [{ type: Input },],
'checkboxesTitles': [{ type: Input },],
'modelsChange': [{ type: Output },],
'errors': [{ type: Input },],
'info': [{ type: Input },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}



