import { User } from '../../shared/models/user.model';
import { Subscription } from 'rxjs/Rx';
import { Component, OnInit, Input, Output, EventEmitter, ComponentFactoryResolver, ViewChild, ElementRef } from '@angular/core';
import { GroupPermission } from './../../shared/models/group-permission.model';
import { ConfirmModalComponent } from '../../modals/confirm-modal/confirm-modal.component';
import { GroupPermissionsService } from '../../shared/group-permissions.service';
import { AppService } from '../../shared/app.service';
import { AccountService } from '../../shared/account.service';
import { ResouceEnumStatus } from '../../shared/enums/resource.enums';
import { MetaModel } from '../../shared/models/meta.model';
import { PermissionsListModalComponent } from '../permissions-grid/permissions-list-modal/permissions-list-modal.component';
import { Permission } from '../../shared/models/permission.model';
import { Group } from '../../shared/models/group.model';
import { PermissionModalComponent } from '../permissions-grid/permission-modal/permission-modal.component';
import { PermissionsService } from '../../shared/permissions.service';
import { ResourcesGridComponent } from '../resources-grid/resources-grid.component';
import { TranslateService } from '@ngx-translate/core';


export class GroupPermissionsGridComponent extends ResourcesGridComponent {

  
  loadAll?: boolean;
  
  onSelectItems: EventEmitter<GroupPermission[] | any>;
  
  focusElement: ElementRef;
  
  group: Group;
  
  public readonly: boolean;

  public modelMeta: any = GroupPermission.meta;
  public items: GroupPermission[];
  public searchText: string = '';
  public selectedItems: GroupPermission[];
  public cachedResourcesService: GroupPermissionsService;

  constructor(
    public groupPermissionsService: GroupPermissionsService,
    public permissionsService: PermissionsService,
    public accountService: AccountService,
    public app: AppService,
    public resolver: ComponentFactoryResolver,
    public translateService: TranslateService
  ) {
    super();
    this.cachedResourcesService = groupPermissionsService.createCache();
  }
  get account(): User {
    return this.accountService.account;
  }
  showCreateModal() {
    if (this.modalIsOpened) {
      return;
    }
    this.modalIsOpened = true;
    let itemModal: PermissionsListModalComponent = this.app.modals(this.resolver).create(PermissionsListModalComponent);
    itemModal.permissions.maxSelectCount = 10000;
    itemModal.account = this.accountService.account;
    itemModal.readonly = this.readonly;
    itemModal.text = this.translateService.instant('Append');;
    itemModal.title = this.translateService.instant('Select permissions for append to group');
    itemModal.onSave.subscribe(($event:any) => this.save($event));
    itemModal.onClose.subscribe(() => this.focus());
    itemModal.item = new Permission();
    itemModal.modal.show();
    this.selectedItems = [new GroupPermission({
      id: itemModal.item.pk,
      permission: itemModal.item
    })];
  }
  showEditModal(item: GroupPermission) {
    if (this.modalIsOpened) {
      return;
    }
    this.modalIsOpened = true;
    let itemModal: PermissionModalComponent = this.app.modals(this.resolver).create(PermissionModalComponent);
    itemModal.account = this.accountService.account;
    itemModal.readonly = !this.account.checkPermissions(['change_permission']) || this.readonly;
    itemModal.text = this.translateService.instant('Save');
    itemModal.title = this.translateService.instant('Edit permission');
    if (itemModal.readonly) {
      itemModal.title = this.translateService.instant('Permission info');
    }
    itemModal.onSave.subscribe(($event:any) => this.savePermission($event));
    itemModal.onClose.subscribe(() => this.focus());
    itemModal.item = item.permission;
    itemModal.modal.show();
  }
  savePermission(itemModal: PermissionModalComponent) {
    this.permissionsService.save(itemModal.item).subscribe(
      (permission: Permission) => {
        itemModal.modal.hide();
      }, (errors: any) => {
        if (errors.message) {
          this.app.component.showErrorModal(errors.message.join(', ')).subscribe(
            () => {
              itemModal.info.emit({ name: '' });
            });
        } else {
          itemModal.errors.emit(errors);
        }
      });
  }
  showRemoveModal(item: GroupPermission) {
    if (this.modalIsOpened) {
      return;
    }
    this.modalIsOpened = true;
    let confirm: ConfirmModalComponent = this.app.modals(this.resolver).create(ConfirmModalComponent);
    confirm.size = 'md';
    confirm.title = this.translateService.instant('Remove');
    confirm.message = this.translateService.instant('Are you sure you want to remove a group permission?');
    confirm.onYes.subscribe(($event:any) => this.remove($event));
    confirm.onClose.subscribe(() => this.focus());
    this.selectedItems = [item];
    confirm.modal.show();
  }
  save(itemModal: PermissionsListModalComponent) {
    this.cachedResourcesService.save(itemModal.items.map(item => new GroupPermission({
      id: item.pk,
      permission: item
    }))).subscribe(
      (groupPermission: GroupPermission) => {
        itemModal.modal.hide();
      }, (errors: any) => {
        if (errors.message) {
          this.app.component.showErrorModal(errors.message.join(', ')).subscribe(
            () => {
              itemModal.info.emit({ name: '' });
            });
        } else {
          itemModal.errors.emit(errors);
        }
      });
  }
  remove(itemModal: ConfirmModalComponent) {
    this.cachedResourcesService.remove(this.selectedItems).subscribe(
      () => {
        itemModal.modal.hide();
      },
      (errors: any) => {
        if (errors.message) {
          this.app.component.showErrorModal(errors.message.join(', ')).subscribe(
            () => {
              this.focus();
            });
        } else {
          itemModal.errors.emit(errors);
        }
      });
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'group-permissions-grid',
  template: `
    <button class="pixel" #focusElement></button>
    <div class="app-grid group-permissions-grid" *ngIf="account">
      <div class="app-grid__toolbar--header">
        <div class="row">
          <div class="col-sm-8">
            <h4>{{modelMeta.group.titles.permissions | translate}}</h4>
          </div>
          <div class="col-sm-4">
            <button type="button" class="btn btn-success pull-right" aria-label="Create" (click)="showCreateModal()" *ngIf="account.checkPermissions(['change_group']) && !readonly">
    					<span class="fa fa-plus"></span> {{ 'Append' | translate }}
    				</button>
          </div>
        </div>
      </div>
      <table class="app-grid__table table table-bordered table-hover">
        <thead>
          <tr>
            <th class="app-grid__table__col-id" [ngClass]="{'select-col':maxSelectCount>1}">{{modelMeta.permission.titles.id | translate}}</th>
            <th class="app-grid__table__col-permission">{{modelMeta.permission.titles.name | translate}}</th>
            <th class="app-grid__table__col-action" *ngIf="account.checkPermissions(['change_permission','change_group']) && !readonly" translate>Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr *ngIf="statusListMessage">
            <td class="app-grid__table__col-status-message" [attr.colspan]="2+(account.checkPermissions(['change_permission','change_group']) && !readonly?1:0)">
              {{statusListMessage | translate}}
            </td>
          </tr>
          <tr *ngFor="let item of items | slice:0:meta.perPage" (click)="selectItem(item, $event)" [ngClass]="{'info':isSelectedItem(item)}"
            (dblclick)="showEditModal(item)" [hidden]="statusListMessage">
            <td class="app-grid__table__col-id" [ngClass]="{'select-col':maxSelectCount>1}">{{item.pk}}</td>
            <td class="app-grid__table__col-permission">{{item.permissionAsString}}</td>
            <td class="app-grid__table__col-action" *ngIf="account.checkPermissions(['change_permission','change_group']) && !readonly">
              <grid-row-buttons (onEdit)="showEditModal(item)" (onRemove)="showRemoveModal(item)" [showEdit]="account.checkPermissions(['change_permission']) && !readonly"
                [showRemove]="account.checkPermissions(['change_group'])"></grid-row-buttons>
            </td>
            </tr>
        </tbody>
      </table>
      <div class="app-grid__toolbar--footer" [hidden]="statusListMessage">
        <div class="row">
          <div class="col-md-3">
          </div>
          <div class="col-md-9">
            <div class="pull-right" *ngIf="meta.totalPages>1 || meta.totalResults>meta.perPage">
              <pagination [directionLinks]="false" (pageChanged)="pageChanged($event)" [boundaryLinks]="true" [totalItems]="meta.totalResults"
                [rotate]="false" [maxSize]="5" [(ngModel)]="meta.curPage" [itemsPerPage]="meta.perPage" class="pagination-md pagination-compressed"
                firstText="&laquo;" lastText="&raquo;"></pagination>
            </div>
          </div>
        </div>
      </div>
    </div>
  `,
  styles: [`
    .group-permissions-grid {

    }
  `],
  entryComponents: [PermissionModalComponent, PermissionsListModalComponent, ConfirmModalComponent]
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: GroupPermissionsService, },
{type: PermissionsService, },
{type: AccountService, },
{type: AppService, },
{type: ComponentFactoryResolver, },
{type: TranslateService, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'loadAll': [{ type: Input },],
'onSelectItems': [{ type: Output },],
'focusElement': [{ type: ViewChild, args: ['focusElement', ] },],
'group': [{ type: Input },],
'readonly': [{ type: Input },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}



