import { Component, OnInit, EventEmitter } from '@angular/core';
import { User } from './../../../shared/models/user.model';
import { AccountService } from './../../../shared/account.service';
import { AppService } from '../../../shared/app.service';
import { TranslateService } from '@ngx-translate/core';


export class ProfileFrameComponent implements OnInit {
  public title: string;
  public modelMeta: any = User.meta;
  public rePassword: string;

  public errors: EventEmitter<any> = new EventEmitter();
  public info: EventEmitter<any> = new EventEmitter();

  constructor(public accountService: AccountService, public app: AppService,
    public translateService: TranslateService) {
  }

  ngOnInit() {
    this.title = `${this.translateService.instant(this.app.currentPageTitle)}: ${this.translateService.instant('Profile')}`;
  }
  get readonly() {
    return !this.account.checkPermissions(['change_profile']);
  }
  get account(): User {
    return this.accountService.account;
  }
  set account(val) {
    this.accountService.account = val;
  }
  update() {
    this.accountService.update(this.account).subscribe(
      (user: User) => {
        this.account = user;
      },
      (errors: any) => {
        if (errors.message) {
          this.app.component.showErrorModal(errors.message.join(', ')).subscribe(
            () => {
              this.info.emit({ username: '' });
            });
        } else {
          this.errors.emit(errors);
        }
      }
    );
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'profile-frame',
  template: `
    <page-header [title]="title"></page-header>
    <form (ngSubmit)="update()" *ngIf="account">
    	<div class="form-group">
    		<label for="inputUsername">{{modelMeta.titles.username | translate}}</label>
    		<input type="text" class="form-control" id="inputUsername" name="inputUsername" placeholder="" [(ngModel)]="account.username"
    			[readonly]="readonly">
    	</div>
    	<div class="form-group">
    		<label for="inputEmail">{{modelMeta.titles.email | translate}}</label>
    		<input type="text" class="form-control" id="inputEmail" name="inputEmail" placeholder="" [(ngModel)]="account.email"
    			[readonly]="readonly">
    	</div>
    	<div class="form-group">
    		<label for="inputPassword">{{modelMeta.titles.password | translate}}</label>
    		<input type="password" class="form-control" id="inputPassword" name="inputPassword" placeholder="" [(ngModel)]="account.password"
    			[readonly]="readonly">
    	</div>
    	<div class="form-group">
    		<label for="inputRePassword">{{modelMeta.titles.rePassword | translate}}</label>
    		<input type="password" class="form-control" id="inputRePassword" name="inputRePassword" placeholder="" [(ngModel)]="account.rePassword"
    			[readonly]="readonly">
    	</div>
    	<div class="form-group">
    		<label for="inputLastName">{{modelMeta.titles.lastName | translate}}</label>
    		<input type="text" class="form-control" id="inputLastName" name="inputLastName" placeholder="" [(ngModel)]="account.lastName"
    			[readonly]="readonly">
    	</div>
    	<div class="form-group">
    		<label for="inputFirstName">{{modelMeta.titles.firstName | translate}}</label>
    		<input type="text" class="form-control" id="inputFirstName" name="inputFirstName" placeholder="" [(ngModel)]="account.firstName"
    			[readonly]="readonly">
    	</div>
    	<button type="submit" class="btn btn-primary" [ngClass]="{'hidden':readonly}" translate>Update</button>
    </form>
  `,
  styles: [`
    .profile-frame {

    }
  `]
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: AccountService, },
{type: AppService, },
{type: TranslateService, },
];
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}



