import {
  Component,
  EventEmitter,
  Input,
  Output,
  ViewChild,
  OnInit
} from '@angular/core';
import { Group } from './../../../shared/models/group.model';
import { ModalDirective } from 'ng2-bootstrap';
import { AccountService } from '../../../shared/account.service';
import { User } from '../../../shared/models/user.model';
import { TextInputComponent } from '../../../controls/text-input/text-input.component';
import { GroupPermissionsGridComponent } from '../../group-permissions-grid/group-permissions-grid.component';
import { Permission } from '../../../shared/models/permission.model';
import { GroupPermission } from '../../../shared/models/group-permission.model';



export class GroupModalComponent implements OnInit {

  
  modal: ModalDirective;
  
  focusElement: TextInputComponent;
  
  groupPermissions: GroupPermissionsGridComponent;
  
  text: string = '';
  
  class: string = '';
  
  readonly: boolean = false;
  
  hideOnClose?: boolean = true;
  
  account: any | User = null;
  
  title: string = '';
  
  item: any | Group = new Group();
  
  public modelMeta: any = GroupPermission.meta;
  
  onClose: EventEmitter<GroupModalComponent> = new EventEmitter<GroupModalComponent>();
  
  onSave: EventEmitter<GroupModalComponent> = new EventEmitter<GroupModalComponent>();

  public errors: EventEmitter<any> = new EventEmitter();
  public info: EventEmitter<any> = new EventEmitter();

  ngOnInit() {
    this.modal.onHidden.subscribe(() => this.close());
    this.modal.onShown.subscribe(() => {
      this.focus();
      this.groupPermissions.group = this.item;
      this.groupPermissions.mockedItems =
        this.item.permissions.map((permission: Permission) => {
          return new GroupPermission({
            id: permission.pk,
            permission: permission
          });
        });
      this.groupPermissions.search();
    });
  }
  focus() {
    this.focusElement.focus();
  }

  close() {
    if (this.hideOnClose && this.modal.isShown) {
      this.modal.hide();
    }
    this.onClose.emit(this);
    return false;
  }
  save() {
    this.item.permissions =
      this.groupPermissions.mockedItems.map((groupPermission: GroupPermission) => groupPermission.permission);
    this.onSave.emit(this);
    return false;
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'group-modal',
  template: `
    <div bsModal #modal="bs-modal" class="modal fade group-modal" [ngClass]="[class]" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel"
      aria-hidden="true">
      <div class="modal-dialog modal-md">
        <div class="modal-content">
          <div class="modal-header">
            <button type="button" class="close" aria-label="Close" (click)="close()">
                        <span aria-hidden="true">&times;</span>
                    </button>
            <h4 class="modal-title">{{title | translate}}</h4>
          </div>
          <form (ngSubmit)="save()">
            <div class="modal-body">
              <text-input [errors]="errors" [info]="info" name="name" [(model)]="item.name" [title]="modelMeta.titles.name" [readonly]="readonly"
                [focused]="true" #focusElement></text-input>
                <group-permissions-grid [loadAll]="false" #groupPermissions [readonly]="readonly"></group-permissions-grid>
            </div>
            <div class="modal-footer">
              <modal-footer-buttons (onClose)="close()" [readonly]="readonly" [okTitle]="text"></modal-footer-buttons>
            </div>
          </form>
        </div>
      </div>
      </div>
  `,
  styles: [`
    .group-modal {
        .field-role{
            display: block;
        }
    }
  `]
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'modal': [{ type: ViewChild, args: ['modal', ] },],
'focusElement': [{ type: ViewChild, args: ['focusElement', ] },],
'groupPermissions': [{ type: ViewChild, args: ['groupPermissions', ] },],
'text': [{ type: Input },],
'class': [{ type: Input },],
'readonly': [{ type: Input },],
'hideOnClose': [{ type: Input },],
'account': [{ type: Input },],
'title': [{ type: Input },],
'item': [{ type: Input },],
'modelMeta': [{ type: Input },],
'onClose': [{ type: Output },],
'onSave': [{ type: Output },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}



