import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { AccountService } from './../../shared/account.service';
import { AppService } from './../../shared/app.service';
import { TranslateService } from '@ngx-translate/core';



export class GridRowButtonsComponent implements OnInit {
  
  editTitle?: string;
  
  removeTitle?: string;
  
  editIcon?: string = 'fa fa-pencil-square-o';
  
  removeIcon?: string = 'fa fa-remove';
  
  editIconColor?: string = ''; // text-primary';
  
  removeIconColor?: string = ''; // 'text-warning';
  
  onEdit: EventEmitter<boolean | any> = new EventEmitter();
  
  onRemove: EventEmitter<boolean | any> = new EventEmitter();
  
  showEdit?: boolean = true;
  
  showRemove?: boolean = true;

  constructor(public translateService: TranslateService) {
    if (this.editTitle === undefined) {
      this.editTitle = this.translateService.instant('Edit');
    }
    if (this.removeTitle === undefined) {
      this.removeTitle = this.translateService.instant('Remove');
    }
  }
  ngOnInit() {
  }

  edit() {
    this.onEdit.emit(true);
    return false;
  }

  remove() {
    this.onRemove.emit(true);
    return false;
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'grid-row-buttons',
  template: `
    <div [ngClass]="{'btn-group':showEdit && showRemove}">
      <button type="button" class="btn btn-default btn-sm" [attr.aria-label]="editTitle" (click)="edit()" *ngIf="showEdit" tooltip="{{editTitle | translate}}"
        placement="left" container="body">
    		<span [ngClass]="[editIcon, editIconColor]"></span>
    	</button>
      <button type="button" class="btn btn-default btn-sm" [attr.aria-label]="removeTitle" (click)="remove()" *ngIf="showRemove"
        tooltip="{{removeTitle | translate}}" placement="left" container="body">
    		<span [ngClass]="[removeIcon, removeIconColor]"></span>
    	</button>
    </div>
  `,
  styles: [`
    .grid-row-buttons-header {

    }
  `]
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: TranslateService, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'editTitle': [{ type: Input },],
'removeTitle': [{ type: Input },],
'editIcon': [{ type: Input },],
'removeIcon': [{ type: Input },],
'editIconColor': [{ type: Input },],
'removeIconColor': [{ type: Input },],
'onEdit': [{ type: Output },],
'onRemove': [{ type: Output },],
'showEdit': [{ type: Input },],
'showRemove': [{ type: Input },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}



