import { User } from '../../shared/models/user.model';
import { Subscription } from 'rxjs/Rx';
import { Component, OnInit, Input, Output, EventEmitter, ComponentFactoryResolver, ViewChild, ElementRef } from '@angular/core';
import { Permission } from './../../shared/models/permission.model';
import { PermissionModalComponent } from './permission-modal/permission-modal.component';
import { ConfirmModalComponent } from '../../modals/confirm-modal/confirm-modal.component';
import { PermissionsService } from '../../shared/permissions.service';
import { AppService } from '../../shared/app.service';
import { AccountService } from '../../shared/account.service';
import { ResouceEnumStatus } from '../../shared/enums/resource.enums';
import { MetaModel } from '../../shared/models/meta.model';
import { ResourcesGridComponent } from '../resources-grid/resources-grid.component';
import { TranslateService } from '@ngx-translate/core';


export class PermissionsGridComponent extends ResourcesGridComponent {

  
  loadAll?: boolean;
  
  onSelectItems: EventEmitter<Permission[] | any>;
  
  focusElement: ElementRef;

  public modelMeta: any = Permission.meta;
  public items: Permission[];
  public searchText: string = '';
  public selectedItems: Permission[];
  public cachedResourcesService: PermissionsService;

  constructor(
    public permissionsService: PermissionsService,
    public accountService: AccountService,
    public app: AppService,
    public resolver: ComponentFactoryResolver,
    public translateService: TranslateService
  ) {
    super();
    this.cachedResourcesService = permissionsService.createCache();
  }
  get account(): User {
    return this.accountService.account;
  }
  get readonly() {
    return !this.account.checkPermissions(['add_permission', 'change_permission', 'delete_permission']);
  }
  showCreateModal() {
    if (this.modalIsOpened) {
      return;
    }
    this.modalIsOpened = true;
    let itemModal: PermissionModalComponent = this.app.modals(this.resolver).create(PermissionModalComponent);
    itemModal.account = this.accountService.account;
    itemModal.readonly = !this.account.checkPermissions(['add_permission']);
    itemModal.text = this.translateService.instant('Create');
    itemModal.title = this.translateService.instant('Create new permission');
    itemModal.onSave.subscribe(($event:any) => this.save($event));
    itemModal.onClose.subscribe(() => this.focus());
    itemModal.item = new Permission();
    itemModal.modal.show();
    this.selectedItems = [itemModal.item];
  }
  showEditModal(item: any | Permission) {
    if (this.modalIsOpened) {
      return;
    }
    this.modalIsOpened = true;
    let itemModal: PermissionModalComponent = this.app.modals(this.resolver).create(PermissionModalComponent);
    itemModal.account = this.accountService.account;
    itemModal.readonly = !this.account.checkPermissions(['change_permission']);
    itemModal.text = this.translateService.instant('Save');
    itemModal.title = this.translateService.instant('Edit permission');
    if (itemModal.readonly) {
      itemModal.title = this.translateService.instant('Permission info');
    }
    itemModal.onSave.subscribe(($event:any) => this.save($event));
    itemModal.onClose.subscribe(() => this.focus());
    itemModal.item = new Permission(item);
    itemModal.modal.show();
    this.selectedItems = [itemModal.item];
  }
  showRemoveModal(item: any | Permission) {
    if (this.modalIsOpened) {
      return;
    }
    this.modalIsOpened = true;
    let confirm: ConfirmModalComponent = this.app.modals(this.resolver).create(ConfirmModalComponent);
    confirm.size = 'md';
    confirm.title = this.translateService.instant('Remove');
    confirm.message = this.translateService.instant('Are you sure you want to remove a permission?');
    confirm.onYes.subscribe(($event:any) => this.remove($event));
    confirm.onClose.subscribe(() => this.focus());
    this.selectedItems = [item];
    confirm.modal.show();
  }
  save(itemModal: PermissionModalComponent) {
    this.cachedResourcesService.save(itemModal.item).subscribe(
      (permission: Permission) => {
        itemModal.modal.hide();
      }, (errors: any) => {
        if (errors.message) {
          this.app.component.showErrorModal(errors.message.join(', ')).subscribe(
            () => {
              itemModal.info.emit({ name: '' });
            });
        } else {
          itemModal.errors.emit(errors);
        }
      });
  }
  remove(itemModal: ConfirmModalComponent) {
    this.cachedResourcesService.remove(this.selectedItems).subscribe(
      () => {
        itemModal.modal.hide();
      },
      (errors: any) => {
        if (errors.message) {
          this.app.component.showErrorModal(errors.message.join(', ')).subscribe(
            () => {
              this.focus();
            });
        } else {
          itemModal.errors.emit(errors);
        }
      });
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'permissions-grid',
  template: `
    <button class="pixel" #focusElement></button>
    <div class="app-grid permissions-grid" *ngIf="account">
      <div class="app-grid__toolbar--header">
        <div class="row">
          <div class="col-md-3">
            <button type="button" class="btn btn-primary" aria-label="Create" (click)="showCreateModal()" *ngIf="account.checkPermissions(['add_permission'])">
    					<span class="fa fa-plus"></span> {{ 'Create' | translate }}
    				</button>
          </div>
          <div class="col-md-9">
            <div class="pull-right">
              <grid-search-panel [(searchText)]="searchText" (onSearch)="search(true)"></grid-search-panel>
            </div>
          </div>
        </div>
      </div>
      <table class="app-grid__table table table-bordered table-hover">
        <thead>
          <tr>
            <th class="app-grid__table__col-id" [ngClass]="{'select-col':maxSelectCount>1}">{{modelMeta.titles.id | translate}}</th>
            <th class="app-grid__table__col-contentType">{{modelMeta.titles.contentType | translate}}</th>
            <th class="app-grid__table__col-codename">{{modelMeta.titles.codename | translate}}</th>
            <th class="app-grid__table__col-name">{{modelMeta.titles.name | translate}}</th>
            <th class="app-grid__table__col-action" *ngIf="account.checkPermissions(['change_permission','delete_permission'])" translate>Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr *ngIf="statusListMessage">
            <td class="app-grid__table__col-status-message" [attr.colspan]="4+(account.checkPermissions(['change_permission','delete_permission'])?1:0)">
              {{statusListMessage | translate}}
            </td>
          </tr>
          <tr *ngFor="let item of items | slice:0:meta.perPage" (click)="selectItem(item, $event)" [ngClass]="{'info':isSelectedItem(item)}"
            (dblclick)="showEditModal(item)" [hidden]="statusListMessage">
            <td class="app-grid__table__col-id" [ngClass]="{'select-col':maxSelectCount>1}">{{item.pk}}</td>
            <td class="app-grid__table__col-contentType">{{item.contentTypeAsString}}</td>
            <td class="app-grid__table__col-codename">{{item.codename}}</td>
            <td class="app-grid__table__col-name">{{item.name}}</td>
            <td class="app-grid__table__col-action" *ngIf="account.checkPermissions(['change_permission','delete_permission'])">
              <grid-row-buttons (onEdit)="showEditModal(item)" (onRemove)="showRemoveModal(item)" [showEdit]="account.checkPermissions(['change_permission'])"
                [showRemove]="account.checkPermissions(['delete_permission'])"></grid-row-buttons>
            </td>
            </tr>
        </tbody>
      </table>
      <div class="app-grid__toolbar--footer" [hidden]="statusListMessage">
        <div class="row">
          <div class="col-md-3">
          </div>
          <div class="col-md-9">
            <div class="pull-right" *ngIf="meta.totalPages>1 || meta.totalResults>meta.perPage">
              <pagination [directionLinks]="false" (pageChanged)="pageChanged($event)" [boundaryLinks]="true" [totalItems]="meta.totalResults"
                [rotate]="false" [maxSize]="5" [(ngModel)]="meta.curPage" [itemsPerPage]="meta.perPage" class="pagination-md pagination-compressed"
                firstText="&laquo;" lastText="&raquo;"></pagination>
            </div>
          </div>
        </div>
      </div>
    </div>
  `,
  styles: [`
    .permissions-grid {

    }
  `],
  entryComponents: [PermissionModalComponent, ConfirmModalComponent]
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: PermissionsService, },
{type: AccountService, },
{type: AppService, },
{type: ComponentFactoryResolver, },
{type: TranslateService, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'loadAll': [{ type: Input },],
'onSelectItems': [{ type: Output },],
'focusElement': [{ type: ViewChild, args: ['focusElement', ] },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}



