import { BrowserModule, DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { User } from '../..//shared/models/user.model';
import { Subscription } from 'rxjs/Rx';
import { Component, OnInit, Input, Output, EventEmitter, ComponentFactoryResolver, ViewChild, ElementRef } from '@angular/core';
import { Fontawesome } from './../../shared/models/fontawesome.model';
import { FontawesomeModalComponent } from './fontawesome-modal/fontawesome-modal.component';
import { ConfirmModalComponent } from '../../modals/confirm-modal/confirm-modal.component';
import { FontawesomesService } from '../../shared/fontawesomes.service';
import { AppService } from '../../shared/app.service';
import { AccountService } from '../../shared/account.service';
import { ResouceEnumStatus } from '../../shared/enums/resource.enums';
import { MetaModel } from '../../shared/models/meta.model';
import { ResourcesGridComponent } from '../resources-grid/resources-grid.component';
import { TranslateService } from '@ngx-translate/core';


export class FontawesomesGridComponent extends ResourcesGridComponent {

  
  loadAll?: boolean;
  
  onSelectItems: EventEmitter<Fontawesome[] | any>;
  
  focusElement: ElementRef;

  public modelMeta: any = Fontawesome.meta;
  public items: Fontawesome[];
  public searchText: string = '';
  public selectedItems: Fontawesome[];
  public cachedResourcesService: FontawesomesService;

  constructor(
    public sanitizer: DomSanitizer,
    public fontawesomesService: FontawesomesService,
    public accountService: AccountService,
    public app: AppService,
    public resolver: ComponentFactoryResolver,
    public translateService: TranslateService
  ) {
    super();
    this.cachedResourcesService = fontawesomesService.createCache();
  }
  safeHtml(html: string) {
    return this.sanitizer.bypassSecurityTrustHtml(html);
  }
  get account(): User {
    return this.accountService.account;
  }
  get readonly() {
    return !this.account.checkPermissions(['add_fontawesome', 'change_fontawesome', 'delete_fontawesome']);
  }
  showCreateModal() {
    if (this.modalIsOpened) {
      return;
    }
    this.modalIsOpened = true;
    let itemModal: FontawesomeModalComponent = this.app.modals(this.resolver).create(FontawesomeModalComponent);
    itemModal.account = this.accountService.account;
    itemModal.readonly = !this.account.checkPermissions(['add_fontawesome']);
    itemModal.text = this.translateService.instant('Create');
    itemModal.title = this.translateService.instant('Create new fontawesome');
    itemModal.onSave.subscribe(($event:any) => this.save($event));
    itemModal.onClose.subscribe(() => this.focus());
    itemModal.item = new Fontawesome();
    itemModal.modal.show();
    this.selectedItems = [itemModal.item];
  }
  showEditModal(item: Fontawesome) {
    if (this.modalIsOpened) {
      return;
    }
    this.modalIsOpened = true;
    let itemModal: FontawesomeModalComponent = this.app.modals(this.resolver).create(FontawesomeModalComponent);
    itemModal.account = this.accountService.account;
    itemModal.readonly = !this.account.checkPermissions(['change_fontawesome']);
    itemModal.text = this.translateService.instant('Save');
    itemModal.title = this.translateService.instant('Edit fontawesome');
    if (itemModal.readonly) {
      itemModal.title = this.translateService.instant('Fontawesome info');
    }
    itemModal.onSave.subscribe(($event:any) => this.save($event));
    itemModal.onClose.subscribe(() => this.focus());
    itemModal.item = new Fontawesome(item);
    itemModal.modal.show();
    this.selectedItems = [itemModal.item];
  }
  showRemoveModal(item: Fontawesome) {
    if (this.modalIsOpened) {
      return;
    }
    this.modalIsOpened = true;
    let confirm: ConfirmModalComponent = this.app.modals(this.resolver).create(ConfirmModalComponent);
    confirm.size = 'md';
    confirm.title = this.translateService.instant('Remove');
    confirm.message = this.translateService.instant('Are you sure you want to remove a fontawesome?');
    confirm.onYes.subscribe(($event:any) => this.remove($event));
    confirm.onClose.subscribe(() => this.focus());
    this.selectedItems = [item];
    confirm.modal.show();
  }
  save(itemModal: FontawesomeModalComponent) {
    this.cachedResourcesService.save(itemModal.item).subscribe(
      (fontawesome: Fontawesome) => {
        itemModal.modal.hide();
      }, (errors: any) => {
        if (errors.message) {
          this.app.component.showErrorModal(errors.message.join(', ')).subscribe(
            () => {
              itemModal.info.emit({ name: '' });
            });
        } else {
          itemModal.errors.emit(errors);
        }
      });
  }
  remove(itemModal: ConfirmModalComponent) {
    this.cachedResourcesService.remove(this.selectedItems).subscribe(
      () => {
        itemModal.modal.hide();
      },
      (errors: any) => {
        if (errors.message) {
          this.app.component.showErrorModal(errors.message.join(', ')).subscribe(
            () => {
              this.focus();
            });
        } else {
          itemModal.errors.emit(errors);
        }
      });
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'fontawesomes-grid',
  template: `
    <button class="pixel" #focusElement></button>
    <div class="app-grid fontawesomes-grid" *ngIf="account">
      <div class="app-grid__toolbar--header">
        <div class="row">
          <div class="col-md-3">
            <button type="button" class="btn btn-primary" aria-label="Create" (click)="showCreateModal()" *ngIf="account.checkPermissions(['add_fontawesome'])">
    					<span class="fa fa-plus"></span> {{ 'Create' | translate }}
    				</button>
          </div>
          <div class="col-md-9">
            <div class="pull-right">
              <grid-search-panel [(searchText)]="searchText" (onSearch)="search(true)"></grid-search-panel>
            </div>
          </div>
        </div>
      </div>
      <table class="app-grid__table table table-bordered table-hover">
        <thead>
          <tr>
            <th class="app-grid__table__col-icon">{{modelMeta.titles.icon | translate}}</th>
            <th class="app-grid__table__col-code">{{modelMeta.titles.code | translate}}</th>
            <th class="app-grid__table__col-class">{{modelMeta.titles.class | translate}}</th>
            <th class="app-grid__table__col-action" *ngIf="account.checkPermissions(['change_fontawesome','delete_fontawesome'])" translate>Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr *ngIf="statusListMessage">
            <td class="app-grid__table__col-status-message" [attr.colspan]="3+account.checkPermissions(['change_fontawesome','delete_fontawesome'])?1:0">
              {{statusListMessage | translate}}
            </td>
          </tr>
          <tr *ngFor="let item of items | slice:0:meta.perPage" (click)="selectItem(item, $event)" [ngClass]="{'info':isSelectedItem(item)}" (dblclick)="showEditModal(item)"
            [hidden]="statusListMessage">
            <td class="app-grid__table__col-icon" [innerHtml]="safeHtml(item.iconAsHtml)"></td>
            <td class="app-grid__table__col-code">{{item.code}}</td>
            <td class="app-grid__table__col-class">{{item.class}}</td>
            <td class="app-grid__table__col-action" *ngIf="account.checkPermissions(['change_fontawesome','delete_fontawesome'])">
              <grid-row-buttons (onEdit)="showEditModal(item)" (onRemove)="showRemoveModal(item)" [showEdit]="account.checkPermissions(['change_fontawesome'])"
                [showRemove]="account.checkPermissions(['delete_fontawesome'])"></grid-row-buttons>
            </td>
            </tr>
        </tbody>
      </table>
      <div class="app-grid__toolbar--footer" [hidden]="statusListMessage">
        <div class="row">
          <div class="col-md-3">
          </div>
          <div class="col-md-9">
            <div class="pull-right" *ngIf="meta.totalPages>1 || meta.totalResults>meta.perPage">
              <pagination [directionLinks]="false" (pageChanged)="pageChanged($event)" [boundaryLinks]="true" [totalItems]="meta.totalResults"
                [rotate]="false" [maxSize]="5" [(ngModel)]="meta.curPage" [itemsPerPage]="meta.perPage" class="pagination-md pagination-compressed"
                firstText="&laquo;" lastText="&raquo;"></pagination>
            </div>
          </div>
        </div>
      </div>
    </div>
  `,
  styles: [`
    .fontawesomes-grid {

    }
  `],
  entryComponents: [FontawesomeModalComponent, ConfirmModalComponent]
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: DomSanitizer, },
{type: FontawesomesService, },
{type: AccountService, },
{type: AppService, },
{type: ComponentFactoryResolver, },
{type: TranslateService, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'loadAll': [{ type: Input },],
'onSelectItems': [{ type: Output },],
'focusElement': [{ type: ViewChild, args: ['focusElement', ] },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}



