import { User } from '../../shared/models/user.model';
import { Subscription } from 'rxjs/Rx';
import { Component, OnInit, Input, Output, EventEmitter, ComponentFactoryResolver, ViewChild, ElementRef } from '@angular/core';
import { UserGroup } from './../../shared/models/user-group.model';
import { ConfirmModalComponent } from '../../modals/confirm-modal/confirm-modal.component';
import { UserGroupsService } from '../../shared/user-groups.service';
import { AppService } from '../../shared/app.service';
import { AccountService } from '../../shared/account.service';
import { ResouceEnumStatus } from '../../shared/enums/resource.enums';
import { MetaModel } from '../../shared/models/meta.model';
import { GroupsListModalComponent } from '../groups-grid/groups-list-modal/groups-list-modal.component';
import { Group } from '../../shared/models/group.model';
import { GroupModalComponent } from '../groups-grid/group-modal/group-modal.component';
import { GroupsService } from '../../shared/groups.service';
import { ResourcesGridComponent } from '../resources-grid/resources-grid.component';
import { TranslateService } from '@ngx-translate/core';


export class UserGroupsGridComponent extends ResourcesGridComponent {

  
  user: User;
  
  loadAll?: boolean;
  
  onSelectItems: EventEmitter<UserGroup[] | UserGroup>;
  
  focusElement: ElementRef;
  
  public readonly: boolean;

  public modelMeta: any = UserGroup.meta;
  public items: UserGroup[];
  public selectedItems: UserGroup[];
  public cachedResourcesService: UserGroupsService;

  constructor(
    public userGroupsService: UserGroupsService,
    public groupsService: GroupsService,
    public accountService: AccountService,
    public app: AppService,
    public resolver: ComponentFactoryResolver,
    public translateService: TranslateService
  ) {
    super();
    this.cachedResourcesService = userGroupsService.createCache();
  }
  get account(): User {
    return this.accountService.account;
  }
  showCreateModal() {
    if (this.modalIsOpened) {
      return;
    }
    this.modalIsOpened = true;
    let itemModal: GroupsListModalComponent = this.app.modals(this.resolver).create(GroupsListModalComponent);
    itemModal.groups.maxSelectCount = 10000;
    itemModal.account = this.accountService.account;
    itemModal.readonly = this.readonly;
    itemModal.text = this.translateService.instant('Append');;
    itemModal.title = this.translateService.instant('Select groups for append to user');
    itemModal.onSave.subscribe(($event:any) => this.save($event));
    itemModal.onClose.subscribe(() => this.focus());
    itemModal.item = new Group();
    itemModal.modal.show();
    this.selectedItems = [new UserGroup({
      id: itemModal.item.pk,
      group: itemModal.item
    })];
  }
  showEditModal(item: UserGroup) {
    if (this.modalIsOpened) {
      return;
    }
    this.modalIsOpened = true;
    let itemModal: GroupModalComponent = this.app.modals(this.resolver).create(GroupModalComponent);
    itemModal.account = this.accountService.account;
    itemModal.readonly = !this.account.checkPermissions(['change_group']) || this.readonly;
    itemModal.text = this.translateService.instant('Save');
    itemModal.title = this.translateService.instant('Edit group');
    if (itemModal.readonly) {
      itemModal.title = this.translateService.instant('Group info');
    }
    itemModal.onSave.subscribe(($event:any) => this.saveGroup($event));
    itemModal.onClose.subscribe(() => this.focus());
    itemModal.item = item.group;
    itemModal.modal.show();
  }
  saveGroup(itemModal: GroupModalComponent) {
    this.groupsService.save(itemModal.item).subscribe(
      (group: Group) => {
        itemModal.modal.hide();
      }, (errors: any) => {
        if (errors.message) {
          this.app.component.showErrorModal(errors.message.join(', ')).subscribe(
            () => {
              itemModal.info.emit({ name: '' });
            });
        } else {
          itemModal.errors.emit(errors);
        }
      });
  }
  showRemoveModal(item: UserGroup) {
    if (this.modalIsOpened) {
      return;
    }
    this.modalIsOpened = true;
    let confirm: ConfirmModalComponent = this.app.modals(this.resolver).create(ConfirmModalComponent);
    confirm.size = 'md';
    confirm.title = this.translateService.instant('Remove');
    confirm.message = this.translateService.instant('Are you sure you want to remove a user group?');
    confirm.onYes.subscribe(($event:any) => this.remove($event));
    confirm.onClose.subscribe(() => this.focus());
    this.selectedItems = [item];
    confirm.modal.show();
  }
  save(itemModal: GroupsListModalComponent) {
    this.cachedResourcesService.save(itemModal.items.map(item => new UserGroup({
      id: item.pk,
      group: item
    }))).subscribe(
      (userGroup: UserGroup) => {
        itemModal.modal.hide();
      }, (errors: any) => {
        if (errors.message) {
          this.app.component.showErrorModal(errors.message.join(', ')).subscribe(
            () => {
              itemModal.info.emit({ name: '' });
            });
        } else {
          itemModal.errors.emit(errors);
        }
      });
  }
  remove(itemModal: ConfirmModalComponent) {
    this.cachedResourcesService.remove(this.selectedItems).subscribe(
      () => {
        itemModal.modal.hide();
      },
      (errors: any) => {
        if (errors.message) {
          this.app.component.showErrorModal(errors.message.join(', ')).subscribe(
            () => {
              this.focus();
            });
        } else {
          itemModal.errors.emit(errors);
        }
      });
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'user-groups-grid',
  template: `
    <button class="pixel" #focusElement></button>
    <div class="app-grid user-groups-grid" *ngIf="account">
      <div class="app-grid__toolbar--header">
        <div class="row">
          <div class="col-sm-8">
            <h4>{{modelMeta.user.titles.groups | translate}}</h4>
          </div>
          <div class="col-sm-4">
            <button type="button" class="btn btn-success pull-right" aria-label="Create" (click)="showCreateModal()" *ngIf="account.checkPermissions(['change_user']) && !readonly">
    					<span class="fa fa-plus"></span> {{ 'Append' | translate }}
    				</button>
          </div>
        </div>
      </div>
      <table class="app-grid__table table table-bordered table-hover">
        <thead>
          <tr>
            <th class="app-grid__table__col-id" [ngClass]="{'select-col':maxSelectCount>1}">{{modelMeta.group.titles.id | translate}}</th>
            <th class="app-grid__table__col-group">{{modelMeta.group.titles.name | translate}}</th>
            <th class="app-grid__table__col-action" *ngIf="account.checkPermissions(['change_group','change_user']) && !readonly" translate>Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr *ngIf="statusListMessage">
            <td class="app-grid__table__col-status-message" [attr.colspan]="2+((account.checkPermissions(['change_group','change_user']) && !readonly)?1:0)">
              {{statusListMessage | translate}}
            </td>
          </tr>
          <tr *ngFor="let item of items | slice:0:meta.perPage" (click)="selectItem(item, $event)" [ngClass]="{'info':isSelectedItem(item)}"
            (dblclick)="showEditModal(item)" [hidden]="statusListMessage">
            <td class="app-grid__table__col-id" [ngClass]="{'select-col':maxSelectCount>1}">{{item.pk}}</td>
            <td class="app-grid__table__col-user">{{item.groupAsString}}</td>
            <td class="app-grid__table__col-action" *ngIf="account.checkPermissions(['change_group','change_user']) && !readonly">
              <grid-row-buttons (onEdit)="showEditModal(item)" (onRemove)="showRemoveModal(item)" [showEdit]="account.checkPermissions(['change_group']) && !readonly"
                [showRemove]="account.checkPermissions(['change_user'])"></grid-row-buttons>
            </td>
            </tr>
        </tbody>
      </table>
      <div class="app-grid__toolbar--footer" [hidden]="statusListMessage">
        <div class="row">
          <div class="col-md-3">
          </div>
          <div class="col-md-9">
            <div class="pull-right" *ngIf="meta.totalPages>1 || meta.totalResults>meta.perPage">
              <pagination [directionLinks]="false" (pageChanged)="pageChanged($event)" [boundaryLinks]="true" [totalItems]="meta.totalResults"
                [rotate]="false" [maxSize]="5" [(ngModel)]="meta.curPage" [itemsPerPage]="meta.perPage" class="pagination-md pagination-compressed"
                firstText="&laquo;" lastText="&raquo;"></pagination>
            </div>
          </div>
        </div>
      </div>
    </div>
  `,
  styles: [`
    .user-groups-grid {

    }
  `],
  entryComponents: [GroupsListModalComponent, GroupModalComponent, ConfirmModalComponent]
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: UserGroupsService, },
{type: GroupsService, },
{type: AccountService, },
{type: AppService, },
{type: ComponentFactoryResolver, },
{type: TranslateService, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'user': [{ type: Input },],
'loadAll': [{ type: Input },],
'onSelectItems': [{ type: Output },],
'focusElement': [{ type: ViewChild, args: ['focusElement', ] },],
'readonly': [{ type: Input },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}



