import { Component, OnInit, Input, EventEmitter, Output, ViewChild, ElementRef } from '@angular/core';



export class TextInputComponent implements OnInit {
  
  inputElement: ElementRef;
  
  focused: boolean = false;
  
  type: string = 'text';
  
  readonly: boolean = false;
  
  name: string = 'text';
  
  placeholder: string = '';
  
  title: string = '';
  
  model: string = '';
  
  modelChange: EventEmitter<string> = new EventEmitter<string>();
  
  errors: EventEmitter<any> = new EventEmitter<any>();
  
  info: EventEmitter<any> = new EventEmitter<any>();
  private errorsValue: any;
  private infoValue: any;
  ngOnInit() {
    this.errors.subscribe((data: any) => {
      this.errorsValue = data;
      let keys = Object.keys(data);
      if (keys[0] === this.name) {
        this.focus();
      }
    });
    this.info.subscribe((data: any) => {
      this.infoValue = data;
      let keys = Object.keys(data);
      if (keys[0] === this.name) {
        this.focus();
      }
    });
  }
  init() {
    setTimeout((out: any) => {
      if (this.focused === true) {
        this.focus();
      }
    }, 700);
  }
  focus() {
    this.inputElement.nativeElement.focus();
  }
  get value() {
    return this.model;
  }
  set value(val) {
    this.model = val;
    this.modelChange.emit(this.model);
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'text-input',
  template: `
    <div class="form-group" [ngClass]="{'has-error':errorsValue && errorsValue[name]}">
      <label class="control-label" [attr.for]="'input'+name">{{title | translate}}</label>
      <input [attr.type]="type" class="form-control" [attr.id]="'input'+name" [attr.name]="'input'+name" [attr.placeholder]="placeholder"
        [(ngModel)]="value" [readonly]="readonly" #inputElement>
      <span class="help-block" *ngIf="errorsValue && errorsValue[name]">{{errorsValue[name].join(', ')}}</span>
    </div>
  `,
  styles: [`
    .text-input {

    }
  `]
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'inputElement': [{ type: ViewChild, args: ['inputElement', ] },],
'focused': [{ type: Input },],
'type': [{ type: Input },],
'readonly': [{ type: Input },],
'name': [{ type: Input },],
'placeholder': [{ type: Input },],
'title': [{ type: Input },],
'model': [{ type: Input },],
'modelChange': [{ type: Output },],
'errors': [{ type: Input },],
'info': [{ type: Input },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}



