import { RouterConfigLoader } from '@angular/router/src/router_config_loader';
import { Component, ComponentFactoryResolver, OnInit } from '@angular/core';
import { AccountService } from '../../shared/account.service';
import { User } from '../../shared/models/user.model';
import { ConfirmModalComponent } from '../../modals/confirm-modal/confirm-modal.component';
import { Router, NavigationStart, Event as NavigationEvent } from '@angular/router';
import { AdminPageComponent } from '../../pages/admin-page/admin-page.component';
import { AppService } from '../../shared/app.service';
import { AuthModalComponent } from '../../modals/auth-modal/auth-modal.component';
import { TranslateService } from '@ngx-translate/core';



export class NavbarComponent implements OnInit {

  constructor(
    public app: AppService,
    public accountService: AccountService,
    public router: Router,
    public resolver: ComponentFactoryResolver,
    public translateService: TranslateService
  ) {
  }

  ngOnInit() {
    this.init();
  }
  init(){
    this.accountService.info();
  }

  get account(): User {
    return this.accountService.account;
  }

  showLogoutModal() {
    if (this.app.modals(this.resolver).exists('loginModal') || this.app.modals(this.resolver).exists('logoutModal')) {
      return;
    }
    let confirm: ConfirmModalComponent = this.app.modals(this.resolver).create(ConfirmModalComponent, 'logoutModal');
    confirm.title = this.translateService.instant('Logout');
    confirm.message = this.translateService.instant('Do you really want to leave?');
    confirm.onYes.subscribe(($event:any) => this.logout($event));
    confirm.modal.show();
  }

  logout(itemModal: ConfirmModalComponent) {
    this.accountService.logout().subscribe(
      () => {
        itemModal.modal.hide();
        this.router.navigate(['/']);
      },
      (errors: any) => {
        if (errors.message) {
          this.app.component.showErrorModal(errors.message.join(', '));
        }
      }
    );
  }

  showLoginModal() {
    if (this.app.modals(this.resolver).exists('loginModal') || this.app.modals(this.resolver).exists('logoutModal')) {
      return;
    }
    let itemModal: AuthModalComponent = this.app.modals(this.resolver).create(AuthModalComponent, 'loginModal');
    itemModal.title = this.translateService.instant('Authorization');
    itemModal.onLogin.subscribe(($event:any) => this.login($event));
    itemModal.modal.show();
  }

  login(itemModal: AuthModalComponent) {
    this.accountService.login(itemModal.account).subscribe(
      (account: any | User) => {
        itemModal.modal.hide();
      },
      (errors: any) => {
        if (errors.message) {
          this.app.component.showErrorModal(errors.message.join(', ')).subscribe(
            () => {
              itemModal.account.username = '';
              itemModal.account.password = '';
              itemModal.focus();
            });
        } else {
          itemModal.errors.emit(errors);
        }
      }
    );
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'navbar',
  template: `
    <!-- Fixed navbar -->
    <nav class="app-navbar navbar navbar-default navbar-fixed-top">
    	<div class="container-fluid">
    		<div class="navbar-header">
    			<button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#navbar" aria-expanded="false"
    				aria-controls="navbar">
    				<span class="sr-only" translate>Menu</span>
    				<span class="icon-bar"></span>
    				<span class="icon-bar"></span>
    				<span class="icon-bar"></span>
    			</button>
    			<a class="navbar-brand" routerLink="/" translate>Site name</a>
    		</div>
    		<div id="navbar" class="navbar-collapse collapse">
    			<ul class="nav navbar-nav navbar-right">
    				<li [class.active]="router.isActive('/admin')" *ngIf="account && account.checkRole('admin')" class="app-navbar__item"><a routerLink="/admin" translate>Admin</a></li>
    				<li [class.active]="router.isActive('/account')" *ngIf="account && account.checkPermissions(['read_accountpage'])" class="app-navbar__item"><a routerLink="/account" translate>Account</a></li>
    				<li [class.active]="router.isActive('/themes')" *ngIf="account" class="app-navbar__item"><a routerLink="/themes" translate>Themes</a></li>
    				<li *ngIf="account" class="pointer">
    					<a (click)="showLogoutModal()" translate>Logout</a>
    				</li>
            <li *ngIf="!account" class="pointer">
    					<a (click)="showLoginModal()" translate>Login</a>
    				</li>
    			</ul>
    		</div>
    		<!--/.nav-collapse -->
    	</div>
    </nav>
  `,
  styles: [`

  `],
  entryComponents: [ConfirmModalComponent, AuthModalComponent]
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: AppService, },
{type: AccountService, },
{type: Router, },
{type: ComponentFactoryResolver, },
{type: TranslateService, },
];
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}



