import { Component, OnInit, Input, EventEmitter, Output, ViewChild, ElementRef } from '@angular/core';



export class RadiosInputComponent implements OnInit {
  
  inputElement: ElementRef;
  
  focused: boolean = false;
  
  readonly: boolean = false;
  
  name: string = 'radios';
  
  title: string = '';
  
  values: { [key: string]: string; } = {};
  
  model: string;
  
  radiosTitles: { [key: string]: string; } = {};
  
  modelChange: EventEmitter<string> = new EventEmitter<string>();

  public value: string;
  
  errors: EventEmitter<any> = new EventEmitter<any>();
  
  info: EventEmitter<any> = new EventEmitter<any>();
  private errorsValue: any;
  private infoValue: any;
  ngOnInit() {
    this.errors.subscribe((data: any) => {
      this.errorsValue = data;
      let keys = Object.keys(data);
      if (keys[0] === this.name) {
        this.focus();
      }
    });
    this.info.subscribe((data: any) => {
      this.infoValue = data;
      let keys = Object.keys(data);
      if (keys[0] === this.name) {
        this.focus();
      }
    });
    this.init();
  }
  init() {
    if (this.model) {
      this.value = this.model;
    } else {
      this.value = this.values[this.keys(this.values)[0]];
    }
    setTimeout((out: any) => {
      if (this.focused === true) {
        this.focus();
      }
    }, 700);
  }
  focus() {
    this.inputElement.nativeElement.focus();
  }
  keys(object: {}) {
    return Object.keys(object);
  }
  updateModel() {
    setTimeout((out: any) => {
      this.model = this.value;
      this.modelChange.emit(this.model);
    }, 700);
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'radios-input',
  template: `
    <div class="form-group radios-input" [ngClass]="{'has-error':errorsValue && errorsValue[name]}">
      <label class="control-label">{{title | translate}}</label>
      <div class="form-control form-radio-controls">
        <label class="control-label checkbox-inline" *ngFor="let key of keys(values)" [hidden]="!radiosTitles[key]">
            <input type="radio" [attr.id]="'input'+key" [(ngModel)]="value" (change)="updateModel()" [value]="values[key]" name="{{name}}" [disabled]="readonly"> {{ radiosTitles[key] | translate }}
        </label>
      </div>
      <span class="help-block" *ngIf="errorsValue && errorsValue[name]">{{errorsValue[name].join(', ')}}</span>
    </div>
  `,
  styles: [`
    .radios-input {
        .form-radio-controls{
            border: 0px;
            padding-right: 0px;
            padding-left: 0px;
            box-shadow: none;
            background-color: transparent;
            .checkbox-inline:first-child{
            //    padding-left:0px;
            }
        }
    }
  `]
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'inputElement': [{ type: ViewChild, args: ['inputElement', ] },],
'focused': [{ type: Input },],
'readonly': [{ type: Input },],
'name': [{ type: Input },],
'title': [{ type: Input },],
'values': [{ type: Input },],
'model': [{ type: Input },],
'radiosTitles': [{ type: Input },],
'modelChange': [{ type: Output },],
'errors': [{ type: Input },],
'info': [{ type: Input },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}



