import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';



export class ModalFooterButtonsComponent implements OnInit {

  
  cancelTitle?: string;
  
  okTitle?: string;
  
  readonly: boolean;
  
  onClose: EventEmitter<boolean | any> = new EventEmitter();

  constructor(public translateService: TranslateService) {
    if (this.cancelTitle === undefined) {
      this.cancelTitle = this.translateService.instant('Cancel');
    }
  }
  ngOnInit() { }

  close() {
    this.onClose.emit(true);
    return false;
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'modal-footer-buttons',
  template: `
    <button type="button" class="btn btn-default" (click)="close()">{{(readonly?'Close':cancelTitle) | translate}}</button>
    <button type="submit" class="btn btn-primary" *ngIf="!readonly">{{okTitle | translate}}</button>
  `,
  styles: [`
    .modal-footer-buttons {

    }
  `]
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: TranslateService, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'cancelTitle': [{ type: Input },],
'okTitle': [{ type: Input },],
'readonly': [{ type: Input },],
'onClose': [{ type: Output },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}



