import { Component, OnInit, Input, Output, ViewChild, EventEmitter, ElementRef, ViewContainerRef } from '@angular/core';
import { ModalDirective } from 'ng2-bootstrap';



export class ConfirmModalComponent implements OnInit {

  
  modal: ModalDirective;
  
  focusElement: ElementRef;
  
  text: string = '';
  
  class: string = '';
  
  hideOnClose?: boolean;
  
  size?: string = 'sm';
  
  title?: string = '';
  
  hideButton?: boolean;
  
  message: string = '';
  
  onClose: EventEmitter<ConfirmModalComponent | any> = new EventEmitter();
  
  onYes: EventEmitter<ConfirmModalComponent | any> = new EventEmitter();

  public errors: EventEmitter<any> = new EventEmitter();
  public info: EventEmitter<any> = new EventEmitter();

  yes() {
    this.onYes.emit(this);
    return false;
  }

  ngOnInit() {
    this.modal.onHidden.subscribe(() => this.close());
    this.modal.onShown.subscribe(() => this.focus());
  }
  init() {
    if (!this.title) {
      this.title = this.text;
    }
  }
  focus() {
    this.focusElement.nativeElement.focus();
  }
  close() {
    if (this.hideOnClose && this.modal.isShown) {
      this.modal.hide();
    }
    this.onClose.emit(this);
    return false;
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'confirm-modal',
  template: `
    <div bsModal #modal="bs-modal" class="modal fade confirm-modal" [ngClass]="[class]" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel"
    	aria-hidden="true">
    	<div class="modal-dialog" [ngClass]="['modal-'+size]">
    		<div class="modal-content">
    			<div class="modal-header">
    				<button type="button" class="close" aria-label="Close" (click)="close()">
                        <span aria-hidden="true">&times;</span>
                    </button>
    				<h4 class="modal-title">{{title | translate}}</h4>
    			</div>
    			<form (ngSubmit)="yes()">
    				<div class="modal-body">
    					{{message | translate}}
    				</div>
    				<div class="modal-footer">
    					<button type="button" class="btn btn-default" (click)="close()" translate>No</button>
    					<button type="submit" #focusElement class="btn btn-primary" translate>Yes</button>
    				</div>
    			</form>
    		</div>
    	</div>
    </div>
  `,
  styles: [`
    .confirm-modal {
    }
  `]
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'modal': [{ type: ViewChild, args: ['modal', ] },],
'focusElement': [{ type: ViewChild, args: ['focusElement', ] },],
'text': [{ type: Input },],
'class': [{ type: Input },],
'hideOnClose': [{ type: Input },],
'size': [{ type: Input },],
'title': [{ type: Input },],
'hideButton': [{ type: Input },],
'message': [{ type: Input },],
'onClose': [{ type: Output },],
'onYes': [{ type: Output },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}



